@import url(https://fonts.googleapis.com/css2?family=Heebo:wght@100;300;400;700&display=swap);
.NavbarContainer {
  background: #191622;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  height: 70px;
  display: flex;
  justify-content: center;
}

a {
  text-decoration: none;

  transition: 0.3s;
}

.LinksContainer {
  width: 30%;
  height: 100%;

  display: flex;
  justify-content: space-evenly;
  align-items: center;

  font: 700 1.1rem "Heebo";
}

.LinksContainer a {
  color: #988bc7;
}

.NavbarContainer a:first-child {
  align-self: center;
  margin-left: 30px;
  font-family: "Heebo", sans-serif;
}

.ProfileContainer {
  background: #191622;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;
}

.ContentWrapper {
  width: 80vw;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.AboutMe {
  width: 60%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.AboutMe * {
  color: #ffffff;
}

.AboutMe button {
  width: 250px;
  height: 70px;
  border-radius: 8px;
  border: solid 2px #988bc7;
  background: none;
  font: 700 1.2rem Heebo;
  color: #988bc7;

  transition: 0.8s;
}

.AboutMe button:hover {
  background: #988bc7;
  color: #191622;
  cursor: pointer;
}

.AboutMe h2 {
  margin-bottom: 30px;
  font: 700 2rem "Heebo";
  letter-spacing: 3px;
}

.AboutMe p {
  margin-bottom: 30px;
  font: 500 1.1rem "Heebo";
}

.Photo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40%;
  height: 100%;
}

.Photo {
  width: 400px;
  height: 400px;

  border-radius: 50%;
  background-image: url("https://avatars.githubusercontent.com/u/67125215?s=400&u=554b73aeced7fd3787d632c6b94a083fa34eeb1b&v=4");
}

.TechsContainer {
  background: #483c67;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;
}

.TechsContainer div {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: red;
  height: 100%;
}

html {
  scroll-behavior: smooth;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #191622;
}

::-webkit-scrollbar-thumb {
  background: #988bc7;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #7363a5;
}

.toTop {
  width: 70px;
  height: 70px;
  border: 4px solid #988bc7;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 0;
  right: 0;
  margin-right: 100px;
  margin-bottom: 100px;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0);
  transition: 0.8s;
}

.toTop:hover {
  filter: brightness(90%);
}

.AboutContentContainer {
  width: 340px;
  height: 100px;

  display: flex;
  justify-content: center;
  flex-direction: column;

  font-family: "Heebo";
}

.SeparatorContainer {
  width: 15%;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  font: 700 1.8rem "Heebo";
}

.AboutMeContainer {
  background: #483c67;
  height: 100vh;

  display: flex;
  align-items: center;
  justify-content: center;
}

.AboutMeContainer .Content {
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  color: #e7e7e7;

  height: 100%;
}
.AboutMeSubHeader {
  margin-top: 50px;
  margin-bottom: 30px;
}
.AboutMeHeader {
  border: 5px solid #e7e7e7;
  border-radius: 8px;
  width: 320px;
  height: 80px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #e7e7e7;
  font: 500 2rem "Heebo";
}

.AboutMeItemsContent {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: #e7e7e7;
}

.AboutMeItems {
  width: 40vw;
  height: 250px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

