@import url("https://fonts.googleapis.com/css2?family=Heebo:wght@100;300;400;700&display=swap");

html {
  scroll-behavior: smooth;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #191622;
}

::-webkit-scrollbar-thumb {
  background: #988bc7;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
  background: #7363a5;
}

.toTop {
  width: 70px;
  height: 70px;
  border: 4px solid #988bc7;
  border-radius: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  bottom: 0;
  right: 0;
  margin-right: 100px;
  margin-bottom: 100px;
  cursor: pointer;
  background-color: rgba(255, 255, 255, 0);
  transition: 0.8s;
}

.toTop:hover {
  filter: brightness(90%);
}
